import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { Card } from 'react-bootstrap';
import {variable} from '../global/url';
import {csrftoken} from '../global/csrfToken';


export default function CategoryDetail({match}) {
    const [category, setCategory] = useState({})
    const id = match.params.id

    useEffect(() => {
        axios({
            method: 'GET',
            url: variable.MainUrl + `api/v1/category/${id}`
        }).then(response => {
            setCategory(response.data)
        })
    }, [id])

    const DeleteCategory = async (e) => {
        if (window.confirm('Delete... Are you sure?')) {
        await axios({
            headers: {"X-CSRFToken": csrftoken },
            method: 'delete',
            url: variable.MainUrl + `api/v1/category/${id}`,
        }).then(response => {
            alert('Category deleted');
            })}
    }
    return(
        <div className="container">
          <Card border="dark">
            <Card.Header>{category.id}</Card.Header>
            <Card.Body>
              <Card.Title>{category.category_title}</Card.Title>
              <Card.Text>
                {category.category_description}
              </Card.Text>

            </Card.Body>
          </Card>
        </div>
    )
}
